<h1 id="应用管理" tabindex="-1"><a class="header-anchor" href="#应用管理" aria-hidden="true">#</a> 应用管理</h1>
<blockquote>
<p>{warning} 企业微信在17年11月对 API 进行了大量的改动，应用管理部分已经没啥用了</p>
</blockquote>
<p>应用管理是企业微信中比较特别的地方，因为它的使用是不基于应用的，或者说基于任何一个应用都能访问这些 API，所以在用法上是直接调用 work 实例的 <code>agent</code> 属性。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token operator">...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><h2 id="应用列表" tabindex="-1"><a class="header-anchor" href="#应用列表" aria-hidden="true">#</a> 应用列表</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$agents</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">agent</span><span class="token operator">-></span><span class="token function">list</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 测试拿不到内容</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="应用详情" tabindex="-1"><a class="header-anchor" href="#应用详情" aria-hidden="true">#</a> 应用详情</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$agents</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">agent</span><span class="token operator">-></span><span class="token function">get</span><span class="token punctuation">(</span><span class="token variable">$agentId</span><span class="token punctuation">)</span><span class="token punctuation">;</span> <span class="token comment">// 只能传配置文件中的 id，API 改动所致</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="设置应用" tabindex="-1"><a class="header-anchor" href="#设置应用" aria-hidden="true">#</a> 设置应用</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$agents</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">agent</span><span class="token operator">-></span><span class="token function">set</span><span class="token punctuation">(</span><span class="token variable">$agentId</span><span class="token punctuation">,</span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'foo'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'bar'</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="设置工作台自定义展示" tabindex="-1"><a class="header-anchor" href="#设置工作台自定义展示" aria-hidden="true">#</a> 设置工作台自定义展示</h2>
<h3 id="模版类型数据结构" tabindex="-1"><a class="header-anchor" href="#模版类型数据结构" aria-hidden="true">#</a> 模版类型数据结构</h3>
<p>可以通过接口配置展示类型。具体可设置:</p>
<ul>
<li>关键数据型</li>
<li>图片型</li>
<li>列表型</li>
<li>webview型</li>
</ul>
<blockquote>
<p>官方文档
https://open.work.weixin.qq.com/api/doc/90000/90135/92535</p>
</blockquote>
<h3 id="设置应用在工作台展示的模版" tabindex="-1"><a class="header-anchor" href="#设置应用在工作台展示的模版" aria-hidden="true">#</a> 设置应用在工作台展示的模版</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$params</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'agentid'</span> <span class="token operator">=></span> <span class="token number">1000005</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'image'</span><span class="token punctuation">,</span> <span class="token comment">//展示类型</span>
    <span class="token string single-quoted-string">'image'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxx'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'jump_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'pagepath'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pages/index'</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'replace_user_data'</span> <span class="token operator">=></span> <span class="token constant boolean">true</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$agents</span><span class="token operator">-></span><span class="token property">agent_workbench</span><span class="token operator">-></span><span class="token function">setWorkbenchTemplate</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$params</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div><h3 id="获取应用在工作台展示的模版" tabindex="-1"><a class="header-anchor" href="#获取应用在工作台展示的模版" aria-hidden="true">#</a> 获取应用在工作台展示的模版</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$agentId</span> <span class="token operator">=</span> <span class="token number">100005</span><span class="token punctuation">;</span>

<span class="token variable">$agents</span><span class="token operator">-></span><span class="token property">agent_workbench</span><span class="token operator">-></span><span class="token function">getWorkbenchTemplate</span><span class="token punctuation">(</span><span class="token keyword type-hint">int</span> <span class="token variable">$agentId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="设置应用在用户工作台展示的数据" tabindex="-1"><a class="header-anchor" href="#设置应用在用户工作台展示的数据" aria-hidden="true">#</a> 设置应用在用户工作台展示的数据</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$params</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'agentid'</span> <span class="token operator">=></span> <span class="token number">1000005</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'userid'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'test'</span><span class="token punctuation">,</span> <span class="token comment">//员工id</span>
    <span class="token string single-quoted-string">'type'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'keydata'</span><span class="token punctuation">,</span> <span class="token comment">//展示类型</span>
    <span class="token string single-quoted-string">'keydata'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'items'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
            <span class="token punctuation">[</span>
                <span class="token string single-quoted-string">'key'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'待审批'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'2'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'jump_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'pagepath'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pages/index'</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>
            <span class="token punctuation">[</span>
                <span class="token string single-quoted-string">'key'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'带批阅作业'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'4'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'jump_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'pagepath'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pages/index'</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>
            <span class="token punctuation">[</span>
                <span class="token string single-quoted-string">'key'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'成绩录入'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'45'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'jump_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'pagepath'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pages/index'</span>
            <span class="token punctuation">]</span><span class="token punctuation">,</span>
            <span class="token punctuation">[</span>
                <span class="token string single-quoted-string">'key'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'综合评价'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'data'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'98'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'jump_url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://www.qq.com'</span><span class="token punctuation">,</span>
                <span class="token string single-quoted-string">'pagepath'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'pages/index'</span>
            <span class="token punctuation">]</span>
        <span class="token punctuation">]</span>
    <span class="token punctuation">]</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$agents</span><span class="token operator">-></span><span class="token property">agent_workbench</span><span class="token operator">-></span><span class="token function">setWorkbenchData</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$params</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br></div></div>